import logo from "./logo.svg";
import "./App.css";
import Contact from "./Components/Contact/Contact";
import { useState } from "react";

function App() {
  const [contactDisplay, setContactDisplay] = useState(false);

  const contactDisplayHandler = () => {
    setContactDisplay(true);
  };

  const closeModalHandler = () => {
    setContactDisplay(false);
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>etCoda</h1>
      </header>
      <div className="body">
        <input
          className="button"
          type="button"
          onClick={contactDisplayHandler}
          value="contact"
        />
        {contactDisplay && <Contact closeModal={closeModalHandler} />}
      </div>
    </div>
  );
}

export default App;
